// LoginViewModel.ts - ViewModel for the login page
import { authService } from "@/services/authService";
import { validateForm, type FormData } from "@/services/validation";

export interface LoginFormData {
  email: string;
  password: string;
}

export interface LoginState {
  isLoading: boolean;
  error: string | null;
  formData: LoginFormData;
  fieldErrors: Record<string, string[]>;
}

export class LoginViewModel {
  private state: LoginState = {
    isLoading: false,
    error: null,
    formData: {
      email: "",
      password: "",
    },
    fieldErrors: {},
  };

  private listeners: Array<() => void> = [];

  constructor() {
    // Initialize state
  }

  // Subscribe to state changes
  subscribe(listener: () => void): () => void {
    this.listeners.push(listener);
    return () => {
      this.listeners = this.listeners.filter((l) => l !== listener);
    };
  }

  private notifyListeners() {
    this.listeners.forEach((listener) => listener());
  }

  // Get current state
  getState(): LoginState {
    return { ...this.state };
  }

  // Update form field
  updateField(field: keyof LoginFormData, value: string): void {
    this.state.formData[field] = value;

    // Clear field error when user starts typing
    if (this.state.fieldErrors[field]) {
      const newFieldErrors = { ...this.state.fieldErrors };
      delete newFieldErrors[field];
      this.state.fieldErrors = newFieldErrors;
    }

    // Clear general error when user starts typing
    if (this.state.error) {
      this.state.error = null;
    }

    this.notifyListeners();
  }

  // Validate form data
  private validateForm(): boolean {
    const formData: FormData = {
      username: this.state.formData.email, // For login, username field is email
      password: this.state.formData.password,
    };

    const validation = validateForm(formData, false); // false = not registration

    this.state.fieldErrors = validation.fieldErrors;

    if (validation.generalErrors.length > 0) {
      this.state.error = validation.generalErrors[0];
    }

    return validation.isValid;
  }

  // Clear all errors
  clearErrors(): void {
    this.state.error = null;
    this.state.fieldErrors = {};
    this.notifyListeners();
  }

  // Set loading state
  private setLoading(isLoading: boolean): void {
    this.state.isLoading = isLoading;
    this.notifyListeners();
  }

  // Set error message
  private setError(error: string): void {
    this.state.error = error;
    this.state.isLoading = false;
    this.notifyListeners();
  }

  // Handle login
  async login(): Promise<boolean> {
    // Clear previous errors
    this.clearErrors();

    // Validate form
    if (!this.validateForm()) {
      this.notifyListeners();
      return false;
    }

    this.setLoading(true);

    try {
      const response = await authService.login({
        email: this.state.formData.email,
        password: this.state.formData.password,
      });

      if (response.success) {
        // Login successful
        this.state.isLoading = false;
        this.notifyListeners();
        return true;
      } else {
        // Login failed
        this.setError(response.message || "Login failed. Please try again.");
        return false;
      }
    } catch (error) {
      console.error("Login error:", error);
      this.setError(
        error instanceof Error
          ? error.message
          : "Network error. Please check your connection.",
      );
      return false;
    }
  }

  // Handle guest login (placeholder)
  async guestLogin(): Promise<boolean> {
    this.setLoading(true);

    try {
      const response = await authService.guestLogin();

      if (response.success) {
        this.state.isLoading = false;
        this.notifyListeners();
        return true;
      } else {
        this.setError(response.message || "Guest login not available.");
        return false;
      }
    } catch (error) {
      console.error("Guest login error:", error);
      this.setError("Guest login failed. Please try again.");
      return false;
    }
  }

  // Reset form
  reset(): void {
    this.state = {
      isLoading: false,
      error: null,
      formData: {
        email: "",
        password: "",
      },
      fieldErrors: {},
    };
    this.notifyListeners();
  }

  // Check if form is valid (for button enabling)
  isFormValid(): boolean {
    const { email, password } = this.state.formData;
    return (
      email.trim().length > 0 &&
      password.trim().length > 0 &&
      Object.keys(this.state.fieldErrors).length === 0
    );
  }

  // Get field error for specific field
  getFieldError(field: keyof LoginFormData): string | null {
    const errors = this.state.fieldErrors[field];
    return errors && errors.length > 0 ? errors[0] : null;
  }

  // Check API connectivity
  async checkApiConnectivity(): Promise<boolean> {
    try {
      return await authService.healthCheck();
    } catch {
      return false;
    }
  }

  // Cleanup
  destroy(): void {
    this.listeners = [];
  }
}

// Export singleton instance for the login page
export const loginViewModel = new LoginViewModel();
