// Authentication service for ABED webapp
export interface LoginRequest {
  email: string;
  password: string;
}

export interface RegisterRequest {
  email: string;
  password: string;
  characterId: string;
  lastNewsReceived: string;
  createdBy: string;
  updatedBy: string;
}

export interface AuthResponse {
  success: boolean;
  message?: string;
  userId?: string;
}

export interface ApiError {
  success: false;
  message: string;
  code?: string;
}

// Base API configuration
const API_BASE_URL = (import.meta.env.VITE_API_URL as string) || "/api";
const API_TIMEOUT = 10000; // 10 seconds

class AuthService {
  private async makeRequest<T>(
    endpoint: string,
    options: RequestInit = {},
  ): Promise<T> {
    const url = `${API_BASE_URL}${endpoint}`;

    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), API_TIMEOUT);

    try {
      const response = await fetch(url, {
        ...options,
        signal: controller.signal,
        headers: {
          "Content-Type": "application/json",
          ...options.headers,
        },
      });

      clearTimeout(timeoutId);

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();
      return data;
    } catch (error) {
      clearTimeout(timeoutId);

      if (error instanceof Error) {
        if (error.name === "AbortError") {
          throw new Error("Request timed out. Please check your connection.");
        }
        throw new Error(error.message);
      }

      throw new Error("An unexpected error occurred");
    }
  }

  /**
   * Login user with email and password
   */
  async login(credentials: LoginRequest): Promise<AuthResponse> {
    try {
      const response = await this.makeRequest<AuthResponse>("/login", {
        method: "POST",
        body: JSON.stringify(credentials),
      });

      return response;
    } catch (error) {
      console.error("Login error:", error);
      return {
        success: false,
        message: error instanceof Error ? error.message : "Login failed",
      };
    }
  }

  /**
   * Register new user account
   */
  async register(userData: RegisterRequest): Promise<AuthResponse> {
    try {
      const response = await this.makeRequest<AuthResponse>("/regist", {
        method: "POST",
        body: JSON.stringify(userData),
      });

      return response;
    } catch (error) {
      console.error("Registration error:", error);
      return {
        success: false,
        message: error instanceof Error ? error.message : "Registration failed",
      };
    }
  }

  /**
   * Guest login (placeholder for future implementation)
   */
  async guestLogin(): Promise<AuthResponse> {
    try {
      const response = await this.makeRequest<AuthResponse>("/guest", {
        method: "POST",
        body: JSON.stringify({}),
      });

      return response;
    } catch (error) {
      console.error("Guest login error:", error);
      return {
        success: false,
        message: error instanceof Error ? error.message : "Guest login failed",
      };
    }
  }

  /**
   * Auto authentication (placeholder for JWT validation)
   */
  async autoAuth(): Promise<AuthResponse> {
    try {
      const response = await this.makeRequest<AuthResponse>("/autoAuth", {
        method: "POST",
        body: JSON.stringify({}),
      });

      return response;
    } catch (error) {
      console.error("Auto auth error:", error);
      return {
        success: false,
        message:
          error instanceof Error ? error.message : "Auto authentication failed",
      };
    }
  }

  /**
   * Logout user
   */
  async logout(): Promise<AuthResponse> {
    try {
      const response = await this.makeRequest<AuthResponse>("/logout", {
        method: "POST",
        body: JSON.stringify({}),
      });

      return response;
    } catch (error) {
      console.error("Logout error:", error);
      return {
        success: false,
        message: error instanceof Error ? error.message : "Logout failed",
      };
    }
  }

  /**
   * Check if the API is reachable
   */
  async healthCheck(): Promise<boolean> {
    try {
      // Simple connectivity test
      const response = await fetch(`${API_BASE_URL}/login`, {
        method: "HEAD",
        signal: AbortSignal.timeout(5000),
      });

      return response.status !== 0;
    } catch {
      return false;
    }
  }
}

// Export singleton instance
export const authService = new AuthService();
export default authService;
