// MusicPlayer.tsx - Persistent floating music player component
import React, { useState, useRef, useEffect } from "react";
import {
  Paper,
  IconButton,
  Slider,
  Box,
  Tooltip,
  Fade,
  Typography,
} from "@mui/material";
import {
  PlayArrow,
  Pause,
  VolumeUp,
  VolumeOff,
  VolumeMute,
  MusicNote,
  ExpandLess,
  ExpandMore,
} from "@mui/icons-material";
import { styled } from "@mui/material/styles";
import { useLocalization, L10N } from "@/localization";

const FloatingPlayer = styled(Paper)(({ theme }) => ({
  position: "fixed",
  top: 20,
  right: 20,
  zIndex: 9999,
  padding: theme.spacing(1),
  minWidth: 60,
  maxWidth: 300,
  background: `linear-gradient(135deg, ${theme.palette.primary.main}20, ${theme.palette.secondary.main}20)`,
  backdropFilter: "blur(12px)",
  border: `1px solid ${theme.palette.divider}`,
  borderRadius: 16,
  boxShadow: "0 8px 32px rgba(0, 0, 0, 0.2)",
  transition: "all 0.3s ease-in-out",
  "&:hover": {
    transform: "translateY(-2px)",
    boxShadow: "0 12px 40px rgba(0, 0, 0, 0.25)",
  },
}));

const CompactView = styled(Box)(() => ({
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  minHeight: 40,
}));

const ExpandedView = styled(Box)(({ theme }) => ({
  padding: theme.spacing(1),
  minWidth: 250,
}));

const VolumeSlider = styled(Slider)(({ theme }) => ({
  color: theme.palette.secondary.main,
  "& .MuiSlider-thumb": {
    width: 16,
    height: 16,
  },
  "& .MuiSlider-track": {
    height: 3,
  },
  "& .MuiSlider-rail": {
    height: 3,
    opacity: 0.3,
  },
}));

const ProgressSlider = styled(Slider)(({ theme }) => ({
  color: theme.palette.primary.main,
  height: 4,
  "& .MuiSlider-thumb": {
    width: 12,
    height: 12,
    "&:hover, &.Mui-focusVisible": {
      boxShadow: `0 0 0 8px ${theme.palette.primary.main}20`,
    },
  },
  "& .MuiSlider-track": {
    height: 4,
    borderRadius: 2,
  },
  "& .MuiSlider-rail": {
    height: 4,
    borderRadius: 2,
    opacity: 0.2,
  },
}));

interface Track {
  title: string;
  artist: string;
  src: string;
  duration: number;
}

// Sample medieval/fantasy tracks (replace with actual game music)
const SAMPLE_TRACKS: Track[] = [
  {
    title: "Tavern Melody",
    artist: "ABED OST",
    src: "/music/tavern-melody.mp3",
    duration: 180, // 3 minutes
  },
  {
    title: "Mystical Forest",
    artist: "ABED OST",
    src: "/music/mystical-forest.mp3",
    duration: 240, // 4 minutes
  },
  {
    title: "Steam Workshop",
    artist: "ABED OST",
    src: "/music/steam-workshop.mp3",
    duration: 200, // 3:20 minutes
  },
];

export const MusicPlayer: React.FC = () => {
  const [isPlaying, setIsPlaying] = useState(false);
  const [isExpanded, setIsExpanded] = useState(false);
  const [volume, setVolume] = useState(0.7);
  const [isMuted, setIsMuted] = useState(false);
  const [currentTime, setCurrentTime] = useState(0);
  const [duration, setDuration] = useState(0);
  const [currentTrackIndex, setCurrentTrackIndex] = useState(0);
  const [isLoading, setIsLoading] = useState(false);

  const audioRef = useRef<HTMLAudioElement>(null);
  const previousVolumeRef = useRef(volume);
  const { t } = useLocalization();

  const currentTrack = SAMPLE_TRACKS[currentTrackIndex];

  // Initialize audio element
  useEffect(() => {
    const audio = audioRef.current;
    if (!audio) return;

    const handleLoadedData = () => {
      setDuration(audio.duration);
      setIsLoading(false);
    };

    const handleTimeUpdate = () => {
      setCurrentTime(audio.currentTime);
    };

    const handleEnded = () => {
      // Auto-play next track
      const nextIndex = (currentTrackIndex + 1) % SAMPLE_TRACKS.length;
      setCurrentTrackIndex(nextIndex);
    };

    const handleLoadStart = () => {
      setIsLoading(true);
    };

    const handleError = () => {
      console.warn("Audio failed to load:", currentTrack.src);
      setIsLoading(false);
      setIsPlaying(false);
    };

    audio.addEventListener("loadeddata", handleLoadedData);
    audio.addEventListener("timeupdate", handleTimeUpdate);
    audio.addEventListener("ended", handleEnded);
    audio.addEventListener("loadstart", handleLoadStart);
    audio.addEventListener("error", handleError);

    return () => {
      audio.removeEventListener("loadeddata", handleLoadedData);
      audio.removeEventListener("timeupdate", handleTimeUpdate);
      audio.removeEventListener("ended", handleEnded);
      audio.removeEventListener("loadstart", handleLoadStart);
      audio.removeEventListener("error", handleError);
    };
  }, [currentTrackIndex]);

  // Update audio volume
  useEffect(() => {
    const audio = audioRef.current;
    if (audio) {
      audio.volume = isMuted ? 0 : volume;
    }
  }, [volume, isMuted]);

  // Update audio source when track changes
  useEffect(() => {
    const audio = audioRef.current;
    if (audio && currentTrack) {
      audio.src = currentTrack.src;
      if (isPlaying) {
        audio.play().catch(console.warn);
      }
    }
  }, [currentTrackIndex]);

  const togglePlay = () => {
    const audio = audioRef.current;
    if (!audio) return;

    if (isPlaying) {
      audio.pause();
    } else {
      audio.play().catch(console.warn);
    }
    setIsPlaying(!isPlaying);
  };

  const toggleMute = () => {
    if (isMuted) {
      setVolume(previousVolumeRef.current);
    } else {
      previousVolumeRef.current = volume;
      setVolume(0);
    }
    setIsMuted(!isMuted);
  };

  const handleVolumeChange = (_: Event, newValue: number | number[]) => {
    const newVolume = Array.isArray(newValue) ? newValue[0] : newValue;
    setVolume(newVolume / 100);
    if (newVolume > 0 && isMuted) {
      setIsMuted(false);
    }
  };

  const handleProgressChange = (_: Event, newValue: number | number[]) => {
    const audio = audioRef.current;
    if (!audio) return;

    const newTime = Array.isArray(newValue) ? newValue[0] : newValue;
    audio.currentTime = (newTime / 100) * duration;
    setCurrentTime(audio.currentTime);
  };

  const formatTime = (seconds: number): string => {
    const mins = Math.floor(seconds / 60);
    const secs = Math.floor(seconds % 60);
    return `${mins}:${secs.toString().padStart(2, "0")}`;
  };

  const getVolumeIcon = () => {
    if (isMuted || volume === 0) return <VolumeOff />;
    if (volume < 0.5) return <VolumeMute />;
    return <VolumeUp />;
  };

  const toggleExpanded = () => {
    setIsExpanded(!isExpanded);
  };

  // const nextTrack = () => {
  //   const nextIndex = (currentTrackIndex + 1) % SAMPLE_TRACKS.length;
  //   setCurrentTrackIndex(nextIndex);
  // };

  // const previousTrack = () => {
  //   const prevIndex = currentTrackIndex === 0
  //     ? SAMPLE_TRACKS.length - 1
  //     : currentTrackIndex - 1;
  //   setCurrentTrackIndex(prevIndex);
  // };

  return (
    <>
      {/* Hidden audio element */}
      <audio ref={audioRef} preload="metadata" loop={false} />

      <FloatingPlayer elevation={8}>
        {!isExpanded ? (
          // Compact view
          <CompactView>
            <Tooltip
              title={
                isPlaying ? t(L10N.musicPlayer.pause) : t(L10N.musicPlayer.play)
              }
            >
              <IconButton
                onClick={togglePlay}
                size="small"
                disabled={isLoading}
                sx={{
                  color: "primary.main",
                  "&:hover": {
                    backgroundColor: "primary.main",
                    color: "primary.contrastText",
                  },
                }}
              >
                {isLoading ? (
                  <MusicNote
                    sx={{ animation: "pulse 1.5s ease-in-out infinite" }}
                  />
                ) : isPlaying ? (
                  <Pause />
                ) : (
                  <PlayArrow />
                )}
              </IconButton>
            </Tooltip>

            <Tooltip title="Expand player">
              <IconButton
                onClick={toggleExpanded}
                size="small"
                sx={{ ml: 0.5, color: "text.secondary" }}
              >
                <ExpandMore />
              </IconButton>
            </Tooltip>
          </CompactView>
        ) : (
          // Expanded view
          <Fade in={isExpanded} timeout={300}>
            <ExpandedView>
              {/* Header with collapse button */}
              <Box
                display="flex"
                justifyContent="space-between"
                alignItems="center"
                mb={1}
              >
                <MusicNote color="primary" />
                <IconButton
                  onClick={toggleExpanded}
                  size="small"
                  sx={{ color: "text.secondary" }}
                >
                  <ExpandLess />
                </IconButton>
              </Box>

              {/* Track info */}
              <Box mb={2}>
                <Typography
                  variant="body2"
                  fontWeight="bold"
                  color="text.primary"
                  noWrap
                >
                  {currentTrack.title}
                </Typography>
                <Typography variant="caption" color="text.secondary" noWrap>
                  {currentTrack.artist}
                </Typography>
              </Box>

              {/* Progress bar */}
              <Box mb={2}>
                <ProgressSlider
                  size="small"
                  value={duration ? (currentTime / duration) * 100 : 0}
                  onChange={handleProgressChange}
                  aria-label="Track progress"
                />
                <Box display="flex" justifyContent="space-between" mt={0.5}>
                  <Typography variant="caption" color="text.secondary">
                    {formatTime(currentTime)}
                  </Typography>
                  <Typography variant="caption" color="text.secondary">
                    {formatTime(duration)}
                  </Typography>
                </Box>
              </Box>

              {/* Controls */}
              <Box
                display="flex"
                alignItems="center"
                justifyContent="space-between"
              >
                {/* Play controls */}
                <Box display="flex" alignItems="center">
                  <Tooltip
                    title={
                      isPlaying
                        ? t(L10N.musicPlayer.pause)
                        : t(L10N.musicPlayer.play)
                    }
                  >
                    <IconButton
                      onClick={togglePlay}
                      disabled={isLoading}
                      sx={{ color: "primary.main" }}
                    >
                      {isLoading ? (
                        <MusicNote
                          sx={{ animation: "pulse 1.5s ease-in-out infinite" }}
                        />
                      ) : isPlaying ? (
                        <Pause />
                      ) : (
                        <PlayArrow />
                      )}
                    </IconButton>
                  </Tooltip>
                </Box>

                {/* Volume controls */}
                <Box display="flex" alignItems="center" flex={1} ml={2}>
                  <Tooltip
                    title={
                      isMuted
                        ? t(L10N.musicPlayer.unmute)
                        : t(L10N.musicPlayer.mute)
                    }
                  >
                    <IconButton
                      onClick={toggleMute}
                      size="small"
                      sx={{ color: "text.secondary" }}
                    >
                      {getVolumeIcon()}
                    </IconButton>
                  </Tooltip>

                  <VolumeSlider
                    size="small"
                    value={isMuted ? 0 : volume * 100}
                    onChange={handleVolumeChange}
                    aria-label={t(L10N.musicPlayer.volumeControl)}
                    sx={{ ml: 1, flexGrow: 1 }}
                  />
                </Box>
              </Box>
            </ExpandedView>
          </Fade>
        )}
      </FloatingPlayer>
    </>
  );
};

export default MusicPlayer;
