import React from "react";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
} from "react-router-dom";
import { ThemeProvider, CssBaseline, GlobalStyles } from "@mui/material";
import { theme, cssVariables } from "@/styles/theme";
import { LoginView } from "@/views/LoginView";
import { RegisterView } from "@/views/RegisterView";
import { MusicPlayer } from "@/components/MusicPlayer";
import { LanguageSwitcher } from "@/components/LanguageSwitcher";

// Global styles for the application
const globalStyles = (
  <GlobalStyles
    styles={{
      ":root": cssVariables,
      "*": {
        boxSizing: "border-box",
      },
      "html, body": {
        margin: 0,
        padding: 0,
        height: "100%",
        fontFamily: "Crimson Text, serif",
        background: `linear-gradient(135deg, ${cssVariables["--color-parchment"]}, ${cssVariables["--color-parchment"]}95)`,
        backgroundAttachment: "fixed",
      },
      "#root": {
        height: "100%",
        display: "flex",
        flexDirection: "column",
      },
      // Import Google Fonts
      "@import": [
        'url("https://fonts.googleapis.com/css2?family=Cinzel:wght@400;500;600;700&family=Crimson+Text:ital,wght@0,400;0,600;1,400&display=swap")',
      ],
      // Custom scrollbar
      "::-webkit-scrollbar": {
        width: 8,
      },
      "::-webkit-scrollbar-track": {
        background: cssVariables["--color-parchment"],
        borderRadius: 4,
      },
      "::-webkit-scrollbar-thumb": {
        background: cssVariables["--color-soft-brown"],
        borderRadius: 4,
        "&:hover": {
          background: cssVariables["--color-steel"],
        },
      },
      // Animations
      "@keyframes pulse": {
        "0%": {
          opacity: 1,
        },
        "50%": {
          opacity: 0.5,
        },
        "100%": {
          opacity: 1,
        },
      },
      "@keyframes fadeIn": {
        from: {
          opacity: 0,
          transform: "translateY(20px)",
        },
        to: {
          opacity: 1,
          transform: "translateY(0)",
        },
      },
      // Page animations
      ".page-enter": {
        animation: "fadeIn 0.3s ease-out",
      },
    }}
  />
);

const App: React.FC = () => {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      {globalStyles}

      <Router>
        <div
          style={{ height: "100vh", display: "flex", flexDirection: "column" }}
        >
          {/* Persistent Music Player - floats above all content */}
          <MusicPlayer />

          {/* Language Switcher - floats in bottom right */}
          <LanguageSwitcher />

          {/* Main content area */}
          <main style={{ flex: 1, display: "flex", flexDirection: "column" }}>
            <Routes>
              {/* Default route redirects to login */}
              <Route path="/" element={<Navigate to="/login" replace />} />

              {/* Login page */}
              <Route path="/login" element={<LoginView />} />

              {/* Register page */}
              <Route path="/register" element={<RegisterView />} />

              {/* Catch-all route redirects to login */}
              <Route path="*" element={<Navigate to="/login" replace />} />
            </Routes>
          </main>
        </div>
      </Router>
    </ThemeProvider>
  );
};

export default App;
